import { Component, OnInit } from '@angular/core';
import { Ideation } from 'src/app/models/ideation';
import { IdeationService } from 'src/app/services/ideation.service';
import { baseURL } from './../../shared/baseurl';
import { Stardevcgroup } from 'src/app/models/stardevcgroup';
import { StardevcgroupService } from 'src/app/services/stardevcgroup.service';

@Component({
  selector: 'app-page-blog-list',
  templateUrl: './page-blog-list.component.html',
  styleUrls: ['./page-blog-list.component.css']
})
export class PageBlogListComponent implements OnInit {
  baseURL: string = baseURL; 
  ideations: Ideation[]=[];
  ideationsInt: Ideation[]=[];
  stardevcgroup: Stardevcgroup;
  indexPaginations: number[] = [];
  startIndex: number = 0;
  endIndex: number;

  constructor( public stardevcgroupService: StardevcgroupService) { }

  ngOnInit(): void {
    this.stardevcgroupService
        .getStardevcgroups()
        .subscribe(data=>{
          this.stardevcgroup = data[0];
          this.ideationsInt = this.stardevcgroup.ideations;
          this.stardevcgroup.ideations.map( ( idea, i) =>  ( i < 2 )? this.ideations.push(idea): this.ideations  );
          this.ideationsInt.forEach( (idea, i) =>  (i < this.ideationsInt.length/2)? this.indexPaginations.push( i ): this.indexPaginations )
          console.log(this.stardevcgroup);
        }) 

    
  }

  nextItem(i) {
    if( i < this.ideationsInt.length )
    {
      this.endIndex = i + 2;
      this.startIndex = i;
      console.log('debut: ' + i + ' fin: ' + this.endIndex);
      this.ideations = this.ideationsInt.slice(i, this.endIndex);
    }
  }

  prevItem(i) {
    if( i > 0 )
    {
      i = i - 1;
      this.endIndex = i + 3;
      this.startIndex = i;
      this.ideations = this.ideationsInt.slice(i + 1, this.endIndex);
    }
  }


}
